<template>
  <div class="result-container">
    <div class="title-wrap">
      <h2 class="title">搜索"{{ $route.query.q }}"结果如下：</h2>
      <!-- 提示区域 -->
      <el-alert title="双击歌曲播放" type="success" center show-icon :closable="true"></el-alert>
    </div>
    <!-- 搜索结果 -->
    <template>
      <el-table
        row-key="id"
        :data="songs"
        style="width: 100%;font-size: 14px"
        @row-dblclick="playMusic"
      >
        <el-table-column type="index">
          <template slot-scope="scope">
            <span>{{ (pageNum - 1) * pageSize + scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column label="音乐标题" width="300">
          <template slot-scope="scope">
            <span>{{ scope.row.name }}</span>
            <span v-if="scope.row.mvid !== 0" class="iconfont icon-mv"></span>
          </template>
        </el-table-column>
        <el-table-column prop="artists[0].name" label="歌手" width="200"></el-table-column>
        <el-table-column prop="album.name" label="专辑" width="400"></el-table-column>
        <el-table-column label="时长">
          <template slot-scope="scope">
            <span>{{ scope.row.duration | playTimeFormat }}</span>
          </template>
        </el-table-column>
      </el-table>
    </template>

    <!-- 分页器 -->
    <el-pagination
      @current-change="handleCurrentChange"
      layout="prev, pager, next"
      :total="total"
      v-if="total > 0"
      :current-page="pageNum"
      :page-size="pageSize"
    ></el-pagination>
  </div>
</template>

<script>
import { search, getMusicUrl } from "@/network/request";
export default {
  data() {
    return {
      total: 0, //歌曲总数
      pageNum: 1, //当前页
      pageSize: 9, //每页数量
      songs: [] //查询到的歌曲
    };
  },
  created() {
    this._search();
  },
  watch: {
    //路由改变时搜索
    $route() {
      this._search();
    }
  },
  methods: {
    // 页码改变事件
    handleCurrentChange(val) {
      // 保存页码
      this.pageNum = val;
      // 重新获取数据
      this._search();
    },

    //查询
    async _search() {
      const { data: resp } = await search(
        this.$route.query.q,
        (this.pageNum - 1) * 9
      );
      if (resp.code !== 200) {
        return;
      }

      this.songs = resp.result.songs;
      this.total = resp.result.songCount;
    },

    async playMusic(row) {
      // 获取歌曲播放地址
      const { data: res } = await getMusicUrl(row.id);
      if (!res.data[0].url) {
        return this.$message.error("该资源为VIP专享，暂不支持播放 ！");
      }
      // 设置给父组件的播放地址
      this.$store.commit("changeUrl", res.data[0].url);
      //跳转歌曲详情页
      this.$router.push(`/song?id=${row.id}`);
    }
  },
  filters: {
    //过滤歌曲时长
    playTimeFormat: function(value) {
      let duration = value;
      let min = parseInt(duration / 1000 / 60);
      if (min < 10) {
        min = "0" + min;
      }
      let sec = parseInt((duration / 1000) % 60);
      if (sec < 10) {
        sec = "0" + sec;
      }
      return `${min}:${sec}`;
    }
  }
};
</script>

<style scoped>
.result-container {
  padding: 0 150px;
}
.icon-mv {
  color: red;
  padding-left: 5px;
}
</style>
